@import url('https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  font-family: "Open Sans", serif;
  height: 100%;
  min-width: 350px;
  overflow: hidden;
}

.main {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100vh;
  width: 100vw;
  background: -webkit-gradient(linear, left top, right top, from(#236), to(#124));
  background: -webkit-linear-gradient(left, #236, #124);
  background: -o-linear-gradient(left, #236, #124);
  background: linear-gradient(to right, #236, #124);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 2%;
}

@media(max-width:850px){
  .main{
    -ms-flex-align: start;
        align-items: flex-start;
    overflow-y: scroll;
  }
}


