.SearchBar{
    z-index: 1;
}

.SearchBar .animation{
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.SearchBar .stoped{
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}

.SearchBar .search_bar{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    height: 10vh;
    margin: 3%;
    border-radius: 10vh;
    background-color: #7657;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.SearchBar .opened{
    width: 94%;
}

.SearchBar .closed{
    width: 10vh;
}

.SearchBar button{
    height: 9vh;
    width: 9vh;
    background: #dca;
    border-radius: 9vh;
    border: none;
    padding: 1.5vh;
    margin: 0.5vh;
    -ms-flex-order: 1;
        order: 1;
    outline: none;
}

.SearchBar input{
    margin: 1vh 1vh 1vh 6vh;
    -ms-flex: 1 1;
        flex: 1 1;
    background-color: #0000;
    border: none;
    outline: none;
    font-size: 5vh; 
}

.SearchBar input::-webkit-input-placeholder{
    color:white;
}

.SearchBar input:-ms-input-placeholder{
    color:white;
}

.SearchBar input::-ms-input-placeholder{
    color:white;
}

.SearchBar input::placeholder{
    color:white;
}

.visible{
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s; 
}

.invisible{
    display: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s; 
}




