.map-container{
    position: relative;
    width: 100%;
    height: 100%;
}

.map {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: grey;
    border: 0px;
}

.map-container p{
    position: absolute;
    z-index: 1;
    bottom: 1vh;
    left: 1vw;
}


