
.title_app{
  width: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  font-size: 1.5vw; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: stretch;
      justify-content: stretch;
}


.title-container__title {
  display: inline-block;
  width: 90%;
  max-width: 30vw;
  background: url("../../assets/img/logo_app.png") center center no-repeat;
  background-size: contain;
  background-position: bottom;
  -ms-flex: 60% 1;
      flex: 60% 1;
}
  
.title-container__subtitle {
  -ms-flex: 40% 1;
      flex: 40% 1;
  font-size: 2em;
  font-style: italic;
  font-weight: 100;
  letter-spacing: 0.1em;
  line-height: 1.5em;
  font-family: "Merriweather", serif;
  text-align: center;
  color: wheat;
  text-shadow:1px px 3px #000;
}

.text_minimized{
  font-size: 1em;
  margin-bottom: 1vh;
}


@media(max-width:850px){
  .title-container__subtitle{
    font-size: 2em;
  }

  .title-container__title {
    max-width: 80vw;
  }
}



